export const validateSaleSession = async (params, db) => {
    const SessionStatus = {
        PROCESSED: 'PROCESSED',
        IN_PROGRESS: 'IN_PROGRESS'
    };
    let sessionInfo = await db.temp_transactions.findOne({
        where: { id: params.session_id },
        raw: true
    });

    if (!sessionInfo) {
        throw { message: 'Invalid transaction' };
    }

    if (sessionInfo.status === SessionStatus.PROCESSED) {
        throw { message: 'Payment done' };
    }

    if (sessionInfo.status != SessionStatus.IN_PROGRESS) {
        console.log('sessionInfo.status', sessionInfo.status);
        throw { message: 'Invalid session' };
    }

    return sessionInfo;
};
