const _ = require('lodash');

const iterateObject = (obj) => {
    var updatedEntries = '';
    for (let property in obj) {
        updatedEntries +=
            '<p style="padding-left:20px; margin:0;"><strong>' + property + ': </strong>' + obj[property] + '</p>';
    }
    return updatedEntries;
};

export const zenDeskUpdateAccountDetailEmailTemplate = (
    updatedAccountDetails,
    merchantName,
    merchantLegalName,
    resellerBrandingObj
) => {
    const updatedDetails = _.isEmpty(updatedAccountDetails)
        ? ''
        : `<div><span style="
                    margin-bottom:0px;
                    margin-top:10px;
                    font-size:14px;
                    ">Updated Details</span>` + iterateObject(updatedAccountDetails);

    return (
        `
        <table style="width: 100%; min-width: 320px; max-width: 600px;">
            <tr>
                <td> 
                    <div style="
                            background-position: center; 
                            background-repeat: no-repeat;
                            background-size: cover;
                            position: relative;
                    ">
                        <img style="
                                border-top-left-radius: 10px;
                                border-top-right-radius: 10px;
                                width: 100%;
                                max-width: 100%;
                                height: 170px;" src="https://email-template-general-images.s3.eu-west-1.amazonaws.com/email-template-images/detail_request.png
                        ">
                    </div>	                      
                    <div class="productDetails">
                        <p><strong>Hi,</strong></p>
                        <p>Please note ${merchantName} ${merchantLegalName} has requested <b>Account Details Update</b>.</p>
                        ` +
        updatedDetails +
        `
                        <p><a href=${resellerBrandingObj.portalURL}>${resellerBrandingObj.portalURL}</a></p>
                    </div>                               
                </td>
            </tr>	            
        </table>
        `
    );
};
