'use strict';
require('dotenv').config();
global.fetch = require('node-fetch').default;

var { response } = require('./index');

const generatePassword = require('password-generator');
const AWS = require('aws-sdk');
const cognitoServiceProvider = new AWS.CognitoIdentityServiceProvider();
const userPoolId = process.env.COGNITO_USER_POOL_ID;

export const createUserWithTemporaryPassword = async (event, db, connectDB, invitation, portalUrl) => {
    let transaction;
    let transactionRds;
    let body;
    let params;
    let cognitoResponse;

    try {
        const { sequelize, User, UserType } = db;

        if (!event.body) {
            const result = response({}, 400);
            return result;
        }

        body = JSON.parse(event.body);
        const generatedPassword = generatePassword(12, false);
        params = {
            UserPoolId: userPoolId,
            Username: body.email,
            ForceAliasCreation: false,
            MessageAction: 'SUPPRESS',
            TemporaryPassword: generatedPassword,
            UserAttributes: [
                {
                    Name: 'email',
                    Value: body.email
                },
                {
                    Name: 'custom:permissions',
                    Value: 'ADD_USER,UPDATE_USER,DELETE_USER,VIEW_ALL'
                },
                {
                    Name: 'custom:roles',
                    Value: 'ROLE_ADMIN,ROLE_MANAGER'
                },
                {
                    Name: 'custom:invited',
                    Value: invitation.toString()
                },
                {
                    Name: 'custom:resellerUrl',
                    Value: portalUrl
                },
                {
                    Name: 'email_verified',
                    Value: 'true'
                }
            ]
        };

        var usersCount = await User.count({
            where: {
                email: body.email
            }
        });

        if (usersCount > 0) {
            throw 'User already exists';
        }

        transaction = await sequelize.transaction();

        var userType = null;

        if (body.isCanonicalResellerMemberInvite) {
            userType = await UserType.findOne({
                where: {
                    name: 'Reseller'
                }
            });
        } else if (!body.lastName && !body.firstName) {
            userType = await UserType.findOne({
                where: {
                    name: 'Merchant'
                }
            });
        }

        const user = User.build({
            email: body.email,
            lastName: body.lastName,
            firstName: body.firstName,
            typeId: userType.id,
            isEmailConfirmed: 1,
            signupStatus: 0
        });

        await user.save({ transaction: transaction });

        cognitoResponse = await createUserInCognito(params);

        const cognitoMyPayRelations = {
            userId: user.id,
            providerId: cognitoResponse.cognitoUserId,
            providerName: 'Cognito'
        };

        const cognitoMyPayRelationsModel = db.IdentityProviderMyPayRelations.build(cognitoMyPayRelations);

        await cognitoMyPayRelationsModel.save({ transaction: transaction });

        await transaction.commit();

        transaction = null;

        if (process.env.IS_OFFLINE) {
            const dbCognito = connectDB(
                process.env.DB_RESOURCE_ARN,
                process.env.INFRA_STAGE + '_database',
                '',
                process.env.SECRET_ARN,
                false
            );

            let usersCountAws = await dbCognito.User.count({
                where: {
                    email: body.email
                }
            });

            if (usersCountAws < 1) {
                transactionRds = await dbCognito.sequelize.transaction();
                const userCognito = dbCognito.User.build(body);
                await userCognito.save({ transaction: transactionRds });

                const cognitoMyPayRelationsModelCognito = dbCognito.IdentityProviderMyPayRelations.build(
                    cognitoMyPayRelations
                );
                cognitoMyPayRelationsModelCognito.userId = userCognito.id;
                await cognitoMyPayRelationsModelCognito.save({ transaction: transactionRds });
                await transactionRds.commit();
            }
        }
        return generatedPassword;
    } catch (error) {
        if (transaction) {
            await transaction.rollback();
        }
        if (transactionRds) {
            await transactionRds.rollback();
        }
        if (cognitoResponse) {
            const paramsDelete = {
                UserPoolId: userPoolId,
                Username: body.email
            };
            await deleteUserInCognito(paramsDelete);
        }
        throw error;
    }
};

const createUserInCognito = (params) => {
    return new Promise((resolve, reject) => {
        cognitoServiceProvider.adminCreateUser(params, (err, data) => {
            if (err) {
                reject(err);
            } else {
                resolve({ cognitoUserId: data.User.Username });
            }
        });
    });
};

const deleteUserInCognito = (params) => {
    return new Promise((resolve, reject) => {
        cognitoServiceProvider.adminDeleteUser(params, (err) => {
            if (err) {
                reject(err);
            } else {
                resolve({});
            }
        });
    });
};
