import { response } from '../response.helper';

export const apiTokenAuthorisation = () => {
    return {
        before: (handler, next) => {
            try {
                const apiToken = handler.event.headers['api_token'];
                if (apiToken) {
                    if (apiToken === process.env.BIFROST_API_KEY) {
                        return next();
                    } else {
                        return Promise.resolve(response({ message: 'Access Denied' }, 403));
                    }
                } else {
                    return Promise.resolve(response({ message: 'API token is missing' }, 401));
                }
            } catch (error) {
                return Promise.resolve(response({ message: 'Access Denied' }, 403));
            }
        },
        after: (handler, next) => {
            if (handler.context) handler.context.callbackWaitsForEmptyEventLoop = false;
            return next();
        },
        onError: (handler, next) => {
            console.error(handler.error);
            handler.response = handler.error;
            return next();
        }
    };
};
