'use strict';
require('dotenv').config();

var { response } = require('./response.helper');

/**
 * @description Return default UserType Id
 */
export const getDefaultUserTypeId = async (db) => {
    const { UserType } = db;

    const userType = await UserType.findOne({
        where: {
            name: 'Merchant'
        }
    });

    if (!userType) {
        return response(
            {
                error: 'User Type is not valid'
            },
            400
        );
    }

    return userType.id;
};
