import { response } from '../response.helper';
import { decodeToken } from '../token-decoder';

/**
 * @param roles string[]
 */
export const userAccessValidatorMiddleware = (identifier) => {
    return {
        before: async (handler, next) => {
            if (identifier === undefined) {
                identifier = 'merchantId';
            }

            try {
                const res = await decodeToken(handler.event)
                    .then((token) => {
                        if (
                            !token.merchants.includes(handler.event.pathParameters[identifier]) &&
                            token.scopes !== 'Admin'
                        ) {
                            return Promise.resolve(response({ message: 'Access Denied' }, 403));
                        }
                        return;
                    })
                    .catch((err) => {
                        console.log(err);
                        handler.response = handler.error;

                        return next();
                    });
                return res;
            } catch (error) {
                return Promise.resolve(response({ message: 'Access Denied' }, 403));
            }
        },
        after: (handler, next) => {
            if (handler.context) handler.context.callbackWaitsForEmptyEventLoop = false;
            return next();
        },
        onError: (handler, next) => {
            console.error(handler.error);
            handler.response = handler.error;
            return next();
        }
    };
};
