const crypto = require('crypto');
const queryString = require('query-string');
const axios = require('axios');

export const processCardStreamPayment = async (payload, signature) => {
    // get the keys in the object
    var items = Object.keys(payload);

    var cs_query_string = '';

    // sort the array of keys
    items.sort();

    // for each key loop over in order
    items.forEach(function (item) {
        cs_query_string += item + '=' + encodeURIComponent(payload[item]) + '&';
    });

    // remove the trailing &
    cs_query_string = cs_query_string.slice(0, -1);

    // below replaces are to ensure the escaping is the same as php's http_build_query()
    cs_query_string = cs_query_string.replace(/\(/g, '%28');
    cs_query_string = cs_query_string.replace(/\)/g, '%29');
    cs_query_string = cs_query_string.replace(/%20/g, '+');

    // make the new string
    payload =
        cs_query_string +
        '&signature=' +
        crypto
            .createHash('SHA512')
            .update(cs_query_string + signature)
            .digest('hex');

    let cs_response = await axios.post('https://gateway.cardstream.com/direct/', payload);
    cs_response = queryString.parse(cs_response.data, { parseNumbers: true });
    return cs_response;
};

export const updateStats = async (params, db) => {
    const { merchant_id, sale_amount, sale_fee, sale_net, currency_code } = params;

    const currentDayStats = await db.stats.findOne({
        where: {
            merchant_id,
            currency_code
        },
        raw: true
    });

    if (currentDayStats) {
        // update
        return db.stats.update(
            {
                sale_count: currentDayStats.sale_count + 1,
                sale_fee: currentDayStats.sale_fee + sale_fee,
                sale_net: currentDayStats.sale_net + sale_net,
                sale_amount: currentDayStats.sale_amount + sale_amount
            },
            {
                where: { id: currentDayStats.id }
            }
        );
    } else {
        return db.stats.create({            
            merchant_id,
            sale_count: 1,
            sale_amount,
            sale_fee,
            sale_net,
            currency_code
        });
    }
};

export const getRedirectInfo = async (params, redirectType = 'SUCCESS') => {
    if (redirectType === 'SUCCESS') {
        let { metaData, sessionInfo, transacRef } = params;
        switch (metaData.from) {
            case 'WP_WC': //wordpress woocommerce
                return {
                    url: metaData.redirect_url,
                    method: 'POST',
                    fields: {
                        responseCode: 0,
                        responseMessage: 'Payment successful',
                        orderRef: sessionInfo.user_order_ref,
                        transactionUnique: transacRef,
                        amount: sessionInfo.amount
                    }
                };
            default:
                return { url: metaData.redirect_url, method: 'GET' };
        }
    } else if (redirectType === 'ERROR') {
        let { metaData, errorInfo } = params;
        switch (metaData.from) {
            case 'WP_WC': //wordpress woocommerce
                return {
                    url: metaData.redirect_url,
                    method: 'POST',
                    fields: {
                        responseCode: errorInfo.code,
                        responseMessage: errorInfo.message
                    }
                };
            default:
                return { url: metaData.redirect_url, method: 'GET' };
        }
    }
};
