import { response } from '../response.helper';
import { decodeToken } from '../token-decoder';
import { UserType } from '../models/index';

/**
 * @param roles string[]
 */
export const userTypesValidatorMiddleware = (roles) => {
    return {
        before: async (handler, next) => {
            try {
                const res = await decodeToken(handler.event)
                    .then((token) => {
                        const tokenRoles = (token.scopes && token.scopes.split(' ')) || [];
                        if (
                            !tokenRoles.length ||
                            !tokenRoles.some((role) => {
                                return (
                                    Object.keys(UserTypePermissionLevel).includes(role) &&
                                    roles.some(
                                        (allowedRole) =>
                                            UserTypePermissionLevel[role] >= UserTypePermissionLevel[allowedRole]
                                    )
                                );
                            })
                        ) {
                            return Promise.resolve(response({ message: 'Access Denied' }, 403));
                        }
                        return;
                    })
                    .catch((err) => {
                        console.log(err);
                        handler.response = handler.error;
                        return next();
                    });
                return res;
            } catch (error) {
                return Promise.resolve(response({ message: 'Access Denied' }, 403));
            }
        },
        after: (handler, next) => {
            if (handler.context) handler.context.callbackWaitsForEmptyEventLoop = false;
            return next();
        },
        onError: (handler, next) => {
            console.error(handler.error);
            handler.response = handler.error;
            return next();
        }
    };
};

const UserTypePermissionLevel = {
    [UserType.ADMIN]: 500,
    [UserType.RESELLER]: 400,
    [UserType.MERCHANT]: 300,
    [UserType.CLIENT]: 200,
    [UserType.BUSINESS]: 100
};
